<script>
import CommodityBtmView from "@/components/search/SearchCommodityView.vue";
import { mapMutations, mapState } from "vuex";

export default {
  components: {
    CommodityBtmView,
  },
  data() {
    return {
      show: false,
      userList: JSON.parse(localStorage.getItem("users")),
      RecommendBlank: null,
    };
  },
  computed: {
    ...mapState(["token"]),
    user() {
      return this.userList.filter((item) => {
        return (item.token = this.token);
      });
    },
  },
  methods: {
    ...mapMutations(["setToken"]),
    showPopup() {
      this.show = true;
    },
    // 查找推荐
    getRecommendBlank() {
      this.axios
        .get("https://apis.netstart.cn/xmsc/home/recommendBlank")
        .then((res) => {
          console.log("查找推荐", res.data.data);
          this.RecommendBlank = res.data.data.recom_list;
        });
    },
    // 退出
    getOut() {
      this.setToken("");
      this.$router.push({
        path: "/login",
        query: { target: this.$route.fullPath },
      });
    },
  },
  created() {
    this.getRecommendBlank();
  },
};
</script>

